import React, { useState, useEffect } from 'preact/compat';
import { Schedule as ScheduleType, EventWithDetails } from '../models/types';
import { loadScheduleData, isScheduleLoaded } from '../utils/apiUtils';
import { getAllEventsWithDetails, sortEventsByDateTime } from '../utils/dataUtils';
import { EventCard } from '../components/EventCard';
import { EventFilter, FilterOptions } from '../components/EventFilter';

export function Schedule() {
  const [schedule, setSchedule] = useState<ScheduleType | null>(null);
  const [events, setEvents] = useState<EventWithDetails[]>([]);
  const [filteredEvents, setFilteredEvents] = useState<EventWithDetails[]>([]);
  const [loading, setLoading] = useState(!isScheduleLoaded());
  const [error, setError] = useState<string | null>(null);
  const [uniqueDates, setUniqueDates] = useState<string[]>([]);
  
  useEffect(() => {
    async function fetchData() {
      try {
        const data = await loadScheduleData();
        setSchedule(data);
        
        // Get all events with details
        const allEvents = getAllEventsWithDetails(data);
        
        // Sort events by date and time
        const sortedEvents = sortEventsByDateTime(allEvents);
        
        setEvents(sortedEvents);
        setFilteredEvents(sortedEvents);
        
        // Extract unique dates for the filter
        const dates = [...new Set(sortedEvents.map(event => event.date))];
        setUniqueDates(dates);
      } catch (err) {
        setError('Failed to load schedule data. Please try again later.');
        console.error(err);
      } finally {
        setLoading(false);
      }
    }
    
    fetchData();
  }, []);
  
  const handleFilterChange = (filters: FilterOptions) => {
    let filtered = [...events];
    
    // Filter by date
    if (filters.date) {
      filtered = filtered.filter(event => event.date === filters.date);
    }
    
    // Filter by location
    if (filters.locationId) {
      filtered = filtered.filter(event => event.locationId === filters.locationId);
    }
    
    // Filter by search term (band name)
    if (filters.searchTerm) {
      const searchLower = filters.searchTerm.toLowerCase();
      filtered = filtered.filter(event => 
        event.band.name.toLowerCase().includes(searchLower) ||
        (event.band.genre && event.band.genre.toLowerCase().includes(searchLower))
      );
    }
    
    setFilteredEvents(filtered);
  };
  
  if (loading) {
    return (
      <div class="flex items-center justify-center h-64">
        <div class="text-center">
          <div class="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-wgt-red mx-auto"></div>
          <p class="mt-4">Loading schedule data...</p>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div class="bg-red-900 text-white p-4 rounded-lg">
        <h2 class="text-xl font-bold mb-2">Error</h2>
        <p>{error}</p>
      </div>
    );
  }
  
  return (
    <div>
      <h1 class="text-3xl font-bold mb-6">Festival Schedule</h1>
      
      {schedule && (
        <EventFilter 
          onFilterChange={handleFilterChange} 
          locations={schedule.locations}
          dates={uniqueDates}
        />
      )}
      
      {filteredEvents.length > 0 ? (
        <div>
          {filteredEvents.map(event => (
            <EventCard key={event.id} event={event} />
          ))}
        </div>
      ) : (
        <div class="card text-center py-8">
          <p class="text-xl text-gray-400">No events found matching your filters.</p>
          <p class="text-gray-500 mt-2">Try adjusting your filter criteria.</p>
        </div>
      )}
    </div>
  );
}